
.router-enter-active,.router-leave-active{
      transition:all 0.5s;
}
.router-enter,.router-leave-to{
    opacity :0;
    transform : scale(0);
}
#app {
  font-family: 'microsoft yahei';
  font-size: 16px;
  color: #000;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}
#app .index-wrap {
  flex: 0 0 90vh;
  overflow: scroll;
  background-color: #f0f0f0;
  -webkit-overflow-scrolling: touch;
}
#app .nav {
  flex: 1;
  display: flex;
  background-color: #333;
}
#app .nav .navitem {
  width: 25vw;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  color: #fff;
  font-size: 15px;
}
#app .nav .navitem .icon {
  font-size: 22px;
}
#app .nav .navitem .icon.active {
  color: #fc1d1c;
}


