
.move-enter-active,.move-leave-active{
      transition: all 0.4s linear;
}
.move-enter,.move-leave-to{
    opacity:0;
    transform: translate3d(100%, 0, 0) rotate(540deg);
}
.control {
  display: flex;
  justify-content: flex-end;
  width: 5rem;
  align-items: center;
}
.control .icon {
  width: 1.3rem;
  height: 1.3rem;
  color: #f00;
}


