
.all-enter-active,.all-leave-active{
      transition : all 2s;
}
.all-enter,.all-leave-to{
      opacity : 0;
}
.item-enter-active,.item-leave-active{
      transition : all 2s;
}
.item-enter,.item-leave-to{
      opacity : 0;
}
.ordernav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  padding-bottom: 0;
  background-color: #fff;
}
.ordernav .item {
  padding-bottom: 10px;
  text-align: center;
  margin: 0 5px;
}
.ordernav .item.active {
  border-bottom: 1px solid #fc2a2a;
  color: #fc2a2a;
}
.order-wrap {
  margin-top: 15px;
}
.order-wrap .order-item {
  margin: 10px 0;
  padding: 1rem;
  background-color: #fff;
}
.order-wrap .order-item .row1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.order-wrap .order-item .row1 .order-id {
  font-size: 1.4rem;
}
.order-wrap .order-item .row1 .order-time {
  text-indent: 5px;
  flex: 1;
  color: #666;
}
.order-wrap .order-item .row1 .order-type {
  font-size: 1.2rem;
  color: #fc2a2a;
}
.order-wrap .order-item .row2 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 2rem;
  line-height: 2rem;
}
.order-wrap .order-item .row3 {
  font-size: 1.3rem;
  height: 2rem;
  line-height: 2rem;
}
.order-wrap .order-item .food-desc {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  font-size: 1rem;
  color: #333;
}
.order-wrap .order-item .food-desc .foodname {
  flex: 0 0 8rem;
}
.order-wrap .order-item .food-desc .foodsum {
  flex: 1;
  text-align: center;
}
.order-wrap .order-item .food-desc .price {
  color: #fc2a2a;
}
.order-wrap .order-item .row4 {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.order-wrap .order-item .row5 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 2rem;
  line-height: 2rem;
}
.order-wrap .order-item .pay-btn-wrap {
  display: flex;
  flex-direction: row-reverse;
}
.order-wrap .order-item .pay-btn-wrap .pay-btn {
  background-color: #fc2a2a;
  color: #fff;
  width: 7rem;
  text-align: center;
  height: 2rem;
  line-height: 2rem;
  border-radius: 10px;
}


